.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.bounding-box:hover{
    background-color: #7ad5ff6c;
}
.container{
    display: flex;
    justify-content: center;
}
